import React, { Component } from 'react';
import { Card, Row, Col, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import { CartHeadAction, ColEditDelete, TableData } from 'components/common/index';
import { getStudentTitles, deleteStudentTitles } from 'services/studentTitle';

class StudentTitles extends Component {
  state = {
    reload: 0,
  };
  handleDelete = async id => {
    await deleteStudentTitles(id);
    message.success('Xa thnh cng');
    this.setState({ reload: this.state.reload + 1 });
  };
  render() {
    const columns = [
      {
        title: 'M chc danh',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tn chc danh',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#/studentTitle/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh sch chc danh">
        <CartHeadAction>
          <BtnAddNew href="#/studentTitle/form/new" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              reload={this.state.reload}
              service={getStudentTitles}
              columns={columns}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <StudentTitles />
  </Page>
);



